import { graphql, Link, useStaticQuery } from 'gatsby';
import React, { useEffect, useState } from 'react';
import { useLocation } from '@reach/router';

export default function Layout({ children }: { children: React.ReactNode }) {
  const location = useLocation();
  const metadata: SiteMetadata = useStaticQuery(graphql`
    query {
      site {
        pathPrefix
      }
      allInfoYaml {
        nodes {
          version
          label
        }
      }
    }
  `);

  function createLink(to: string): string {
    if (location.protocol === 'file:') {
      const prefix = metadata.site.pathPrefix + '/public';
      const position = location.href.indexOf(prefix);
      const baseUrl = location.href.slice(0, position + prefix.length);
      return `${baseUrl}/${to}/index.html`;
    } else {
      return `/${metadata.site.pathPrefix}/${to}`;
    }
  }

  return (
    <div>
      <div className="flex flex-row space-x-2 px-2 py-3">
        {metadata.allInfoYaml.nodes.map((item) => (
          <Link key={item.version} to={createLink(item.version)}>
            {item.label}
          </Link>
        ))}

        <div>| {JSON.stringify(location)} |</div>
      </div>
      <div className="px-2">{children}</div>
    </div>
  );
}

type SiteMetadata = {
  site: { pathPrefix: string };
  allInfoYaml: {
    nodes: { version: string; label: string }[];
  };
};
